/* SETUP STYLES */

article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block;}audio,canvas,video{display:inline-block;}audio:not([controls]){display:none;height:0;}[hidden]{display:none;}html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;}a:focus{outline:thin dotted;}a:active,a:hover{outline:0;}h1{font-size:2em;}abbr[title]{border-bottom:1px dotted;}b,strong{font-weight:700;}dfn{font-style:italic;}mark{background:#ff0;color:#000;}code,kbd,pre,samp{font-family:monospace, serif;font-size:1em;}pre{white-space:pre-wrap;word-wrap:break-word;}q{quotes:\201C \201D \2018 \2019;}small{font-size:80%;}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}sup{top:-.5em;}sub{bottom:-.25em;}img{border:0;}svg:not(:root){overflow:hidden;}fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em;}button,input,select,textarea{font-family:inherit;font-size:100%;margin:0;}button,input{line-height:normal;}button,html input[type=button],/* 1 */input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;}button[disabled],input[disabled]{cursor:default;}input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;}input[type=search]{-webkit-appearance:textfield;box-sizing:content-box;}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none;}textarea{overflow:auto;vertical-align:top;}table{border-collapse:collapse;border-spacing:0;}body,figure{margin:0;}legend,button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0;}

.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { box-sizing: border-box; }

.visuallyHidden {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
    padding:0 !important;
    border:0 !important;
    height: 1px !important; 
    width: 1px !important; 
    overflow: hidden;
}
html {
  font-size: 125%;
}

body {
    text-align: center;
    font-size: 1rem;
    font-family: 'Hind Siliguri', sans-serif;
}

.wrapper {
  width: 90%;
  max-width: 1100px;
  margin: 0 auto;
}

h1, h2, h3 {
  font-family: 'Cormorant', serif;  
}

h1 {
  font-weight: 500;
  font-size: 5rem;
  text-transform: uppercase;
}

p {
  font-family: 'Hind Siliguri',sans-serif;
}

header {
  background: url(/ethicalMakeup/static/media/image.7adc29ad.jpg) no-repeat;
  background-size: cover;
  background-position: center top;
  height: 100vh;
}

img {
  max-width: 100%;
  display: block;
  object-fit: cover;
}

main {
  background: white;
}

.button {
  background-color: #4CD3AA;
  border: none;
  border-radius: 10px;
  color: white;
  padding: 5px 30px;
  text-decoration: none;
  box-shadow: 2px 2px 5px 1px rgba(117,100,117,1);
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
  clear: both;
  display: inline-block;
}

.button:active {
        box-shadow:  -1px -1px 0px -3px;
}
.button:hover {
    background-color: #3FBC96;
}

/* ANIMATION */

@-webkit-keyframes fade-in {
    from {
      opacity: 0;
      -webkit-transform: translateY(405px);
              transform: translateY(405px);
    }
    to {
      opacity: 2;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }

@keyframes fade-in {
    from {
      opacity: 0;
      -webkit-transform: translateY(405px);
              transform: translateY(405px);
    }
    to {
      opacity: 2;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }


h1 {
    margin: 0;
    padding-top: 50px;
    color: #CD9875;
}

select {
    border: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background: transparent;
    margin-left: 10px;
    border-bottom: 2px solid #4CD3AA;
    position: relative;
    width: 210px;
    background: url(/ethicalMakeup/static/media/arrow.3ec50539.svg) no-repeat;
    background-position: right center;
}

select:focus {
    background: #CADFD4;
}

.landing {
    display: inline-block;
    padding: 10px 20px;
    background: rgba(255,255,255,0.9);
    border-radius: 15px;
}

@media only screen and (max-width:900px) {

    h1 {
        font-size: 4rem;
        padding-top: 40px;
    }

}

@media only screen and (max-width:748px) {

    h1 {
        font-size: 3.6rem;
        margin-bottom: 20px;
    }
}

@media only screen and (max-width:498px) {
    h1 {
        font-size: 3rem;
    }

    select {
        text-align-last: center;
    }

}
ul {
	list-style-type: none;
	overflow: hidden;
	margin: 0 auto;
    padding: 0;
    text-align: center;
}

li {
	width: calc(100% / 3 - 20px);
	margin: 30px 10px 10px 10px;
	float: left;
	word-wrap: break-word;
	cursor: pointer;
	background: #ffffff;
	-webkit-transition: all 0.7s ease;
	transition: all 0.7s ease;
    opacity: 0;
    -webkit-animation: fade-in ease 3s forwards;
            animation: fade-in ease 3s forwards;
    height: 350px;
    padding: 5px;
}

li:hover,
li:focus {
	box-shadow: 0px 0px 6px 6px rgba(205, 152, 117, 0.75);
}

.title {
    font-family: 'Cormorant', serif;
    font-size: 1rem;
}

.imageContainer img {
    margin: 5px auto;
    object-fit: cover;
    height: 200px;
}

.title,
.price,
.none {
	margin: 10px;
}

.none {
    font-style: italic;
}

.price,
.none {
    font-size: .8rem;
}

@media only screen and (max-width: 718px) {
	li {
		width: calc(100% / 2 - 20px);
	}
}

@media only screen and (max-width: 480px) {
    li {
        width: 90%;
    }

    li:hover {
        -webkit-transform: none;
                transform: none;
    }

    ul {
        padding:0;
    }
}

span {
    height: 30px;
    width: 30px;
    display: inline-block;
}

.image {
    width: calc(20% - 30px);
    float: left;
    margin: 30px 30px 10px 0;
}

.image img {
    width: 200px;
    margin: 0 auto;
}

.description {
    width: 80%;
    float: left;
    text-align: left;
}

.description p {
    margin: 10px 0;
}

.description h2 {
    text-align: center;
}

.description-card {
    padding-top: 5%;
    border-top: 3px solid #4CD3AA; 
    -webkit-animation: fade-in ease 2s forwards; 
            animation: fade-in ease 2s forwards;
    margin-top: 10px;
}

.descriptionText {
    max-height: 250px;
    overflow: scroll;
}

.colours {
    width: 10%;
    float: left;
    text-align: left;
    margin: 10px 10px;
}

.colours p {
    margin-bottom: 0;
}

.description .button {
    margin: 25px 0;
}

.noRating span {
    font-style: italic;
    width: 180px;
    font-size: 0.9rem;
}

.colour {
    border-radius: 0.8rem 0.7rem 0.6rem;
}

@media only screen and (max-width: 620px) {

    .image {
        float: none;
        width: 100%;
        text-align: center;
    }

    .description {
        text-align-last: center;
        width: 100%;
    }

    .description h2 {
        margin-bottom: 5px;
    }

    .descriptionText {
       font-size: 0.8rem;
       text-align-last: left;
    }

}
.form {
    box-shadow: 0 0 10px #999;
}

.formReview {
    color:#261c16;
    padding: 5%;
    margin-top: 2%;
    font-family: 'Hindi Siliguri', sans-serif;
    font-size: 1rem;
    overflow: hidden;
    width: 100%;
}

.formName,
.buyAgain {
    float: left;
    width: 100%;
    margin-bottom: 20px;
}

.formName {
    text-align: center;
}

.formName input {
    border: 0;
    padding: 2%;
    box-shadow: -2px 2px #CD9875;
}

.buyAgain {
    text-align: center;
    width: 100%;
}

.buyAgain p {
    margin: 0;
    margin-bottom: 10px;
}

.buyAgainAnswer {
    width: 60%;
    text-align: center;
    margin: 10px auto;
}

.buyAgainAnswer input[type="radio"] {
    margin-top: -1px;
    vertical-align: middle;
  }

.buyAgainAnswer label {
    margin-right: 9%;
    margin-left: 3%;
}

.formCommentField,
.formButton {
    float: left;
    width: 100%;
}

.formCommentField {
    text-align: center;
}

.formCommentField textarea {
    width: 50%;
    border: 0;
    padding: 2%;
    box-shadow: -2px 2px #CD9875;
    resize: none;
}

.formButton {
    text-align: center;
    margin-top: 2%;
}

/* MEDIA QUERY 630px */

 @media (max-width: 630px) {

    .formCommentField textarea {
        width: 80%;
    }

    .formReview {
        font-size: 0.9rem;
    }

} 



.reviews .wrapper {
    text-align: left;
}

.reviews h2 {
    text-align: left;
    text-transform: uppercase;
}

.reviews .button {
    margin: 25px 0;
    padding: 10px 30px;

}

.reviews .form {
    background: url('http://api.thumbr.it/whitenoise-361x370.png?background=c7ded2ff&noise=cc9674&density=3&opacity=100');   
}

.review {
    overflow: hidden;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    padding: 20px 0;
}

.review h3, p {
    margin: 0;
}

.review p {
    float: left;
    clear: both;
}

.review h3 {
    float: left;
}

.review .date {
    float: right;
    color: grey;
    clear: none;
}

.choice {
    font-style: italic;
    margin: 10px 0;
    font-size: 0.8rem;
}
.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1s infinite ease-in-out;
  animation: load7 1s infinite ease-in-out;
}
.loader {
  color: #cea990;
  font-size: 10px;
  margin: 0 auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}
.loader:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
  color: #cd9875;
}
.loader:after {
  left: 3.5em;
  color: #cfbeb3 ;
}
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

